import {Building, BuildingRarity, BuildingType} from "../Building";
import {Buff, BuffRange} from "../Buff";

class WaterPlant extends Building{
    constructor(){
        super("",BuildingRarity.Common,BuildingType.Industrial,1.26);
    }

    initBuffs(){
        this.buffs.push(new Buff(BuffRange.Offline,BuffRange.Offline,0.05));
    }

    getBuffValue(buff) {
        //buff0.1/0.15/0.2/0.25/0.3
        //
        return super.getBuffValue(buff) + 0.05;
    }
}

export default WaterPlant